  }
}
.footer__logo:focus {
  outline: none;
}
.footer__logo:hover, .footer__logo:focus-visible {
  opacity: 0.6;
}
.footer__logo:active {
  opacity: 0.4;
}

.footer__tel {
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 600;
  font-size: 18px;
  line-height: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.3s ease;
  flex-wrap: wrap;
}
@media (max-width: 1199px) {
  .footer__tel {
    display: none;
  }
}
.footer__tel:focus {
  outline: none;
}
.footer__tel:hover, .footer__tel:focus-visible {
  color: #1C3374;
}
.footer__tel:active {
  color: #0266c1;
}
.footer__tel--disabled {
  pointer-events: none;
  color: #0266c1;
}

.footer__politics {
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 300;
  font-size: 12px;
  line-height: 12px;
  position: relative;
  display: flex;
  margin: 0 auto;
  flex-wrap: wrap;
  top: -6px;
}

.social {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  max-width: 200px;
  gap: 10px 15px;
  list-style-type: none;
  padding: 0;
  margin: 0 auto;
  flex-wrap: wrap;
}

.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  color: #2d383f;
  border: 1px solid rgba(45, 56, 63, 0.3);
  transition: 0.3s ease;
}
.social__link:focus {
  outline: none;
}
.social__link:hover, .social__link:focus-visible {
  border: 1px solid #2d383f;
}
.social__link:active {
  color: #ffffff;
  background-color: #0266c1;
  border: 0;
}
.social__link--disabled {
  pointer-events: none;
  border: 1px solid rgba(45, 56, 63, 0.3);
  color: rgba(45, 56, 63, 0.3);
}

